
angular.module("cloudbinc").controller("homeController", ["$scope","$timeout","dataService",

  function($scope, $timeout, dataService) {
      $scope.isHide=true;
      $scope.isTable=true

      $scope.content = dataService.getData();

      $scope.searchedData={}
      $scope.connectDB = function(){
        var query={'query':$scope.query}
        dataService.getConnectDB(query).then(function (resp) {
            console.log("RRRRRRR");
            console.log(resp);
            $scope.searchedData= resp.data;

        })
      }



    $scope.contentItems = []
    $scope.loadItems = function(c) {
        $scope.contentItems = c.items
    }



  }

]);
