

angular.module("cloudbinc").controller("searchQueryController", ["$scope","dataService",
    function($scope, dataService) {
        console.log('hiihihihihihihih')
        // var a = this;
        // a.is_error = false;
      $scope.searchedData={}
      $scope.connectDB = function(){
        var query={'query':$scope.query,'startdate':$scope.startdate,'enddate':$scope.enddate,'event':$scope.event}
        dataService.getCreateQuery(query).then(function (resp) {
            console.log("RRRRRRR");
            console.log(resp);
            $scope.searchedData= resp.data;

        })
      }


    }
]);
