
angular.module("cloudbinc").controller("dashcontroller", ["$scope",

  function($scope) {

      $scope.loadBarChart = function(){
        $scope.options = {
              chart: {
                  type: 'discreteBarChart',
                  height: 450,
                  margin : {
                      top: 20,
                      right: 20,
                      bottom: 60,
                      left: 55
                  },
                  x: function(d){ return d.label; },
                  y: function(d){ return d.value; },
                  showValues: true,
                  valueFormat: function(d){
                      return d3.format(',.4f')(d);
                  },
                  transitionDuration: 500,
                  xAxis: {
                      axisLabel: 'X Axis'
                  },
                  yAxis: {
                      axisLabel: 'Y Axis',
                      axisLabelDistance: 30
                  }
              }
        };


        $scope.data = [
          {
          key: "Cumulative Return",
          values: [
            { "label" : "A" , "value" : 29.76 },
            { "label" : "B" , "value" : 30 },
            { "label" : "C" , "value" : 32.807 },
            { "label" : "D" , "value" : 46.459 },
            { "label" : "E" , "value" : 39 },
            { "label" : "F" , "value" : 48.07 },
            { "label" : "G" , "value" : 33.92 },
            { "label" : "H" , "value" : 45.13 }
          ]
        }
        ];

      }



      $scope.loadPieChart = function(){
        $scope.optionsPie = {
            chart: {
                type: 'pieChart',
                height: 500,
                x: function(d){return d.key;},
                y: function(d){return d.y;},
                showLabels: true,
                duration: 500,
                labelThreshold: 0.01,
                labelSunbeamLayout: true,
                legend: {
                    margin: {
                        top: 5,
                        right: 35,
                        bottom: 5,
                        left: 0
                    }
                }
            }
        };



        $scope.dataPie = [
            {
                key: "One",
                y: 5
            },
            {
                key: "Two",
                y: 2
            },
            {
                key: "Three",
                y: 9
            },
            {
                key: "Four",
                y: 7
            },
            {
                key: "Five",
                y: 4
            },
            {
                key: "Six",
                y: 3
            },
            {
                key: "Seven",
                y: .5
            }
        ];
      }

      $scope.loadPieChart();
      $scope.loadBarChart();







  }

]);
