angular.module("cloudbinc").controller("textSearchController", ["$scope","dataService",
    function($scope, dataService) {
        console.log('hiihihihihihihih')

        // var a = this;
        // a.is_error = false;
      $scope.searchedData=[]
      $scope.showcharts = false

      $scope.btnText ='Connect DB'
      $scope.isDisable =false

      $scope.connectDB = function(){
        $scope.btnText ='Loading...'
        // $scope.isDisable =true


        var query={'query':$scope.query,'startdate':$scope.startdate,'enddate':$scope.enddate,'event':$scope.event}
        dataService.searchQuery(query).then(function (resp) {
        console.log('you are in search query in textsearchcont')
        console.log(query)
        console.log(resp);
        $scope.searchedData= resp.data.resp;

        $scope.newCreateQuery= resp.data.query;
        $scope.btnText ='Connect DB'
          $scope.isDisable =false
        $scope.datakeys = Object.keys(resp.data.resp[0]);
        console.log("$scope.datakeys")
        console.log($scope.datakeys)
        $scope.dataPieArray = []
        $scope.barData = [{
          key:"Bar chart",
          values:[]
        }]
        var bardataArr=[]


          console.log('search data')
          console.log($scope.searchedData)

          for(var i=0; i<$scope.searchedData.length;i++) {
            console.log('You are in forr looop')
            var keys=''
            var newKeys= Object.keys($scope.searchedData[i]);
            console.log("newKey")
            console.log(newKeys)
            for(var c=0;c<newKeys.length;c++){
              if($scope.datakeys.indexOf(newKeys[c]) == -1) {
                $scope.datakeys.push(newKeys[c]);
              }
              console.log(newKeys)
              if(newKeys[c]!='count'){
                if($scope.searchedData[i][newKeys[c]]!= undefined){
                  keys +=  $scope.searchedData[i][newKeys[c]] +', '
                }
                }
            }


            var dataDict = {
              'key':keys,
              'y': parseInt($scope.searchedData[i]['count'])
            }

            var tmpDicts = {
              'label' : keys,
              'value' : parseInt($scope.searchedData[i]['count'])
            }
            console.log('print data dict')
            console.log(dataDict)
            bardataArr.push(tmpDicts)
            $scope.dataPieArray.push(dataDict)

          }
          console.log("$scope.dataPieArray")
          console.log($scope.dataPieArray)
          $scope.barData[0].values =bardataArr
          console.log("$scope.barData")
          console.log($scope.barData)
          $scope.loadPieChart();
          $scope.loadBarChart();
        })
      }


        console.log('Print the value Array')
        console.log($scope.valueArray)

      $scope.loadBarChart = function(){
        $scope.options = {
              chart: {
                  type: 'discreteBarChart',
                  height: 450,
                  margin : {
                      top: 20,
                      right: 20,
                      bottom: 100,
                      left: 55
                  },
                  x: function(d){ return d.label; },
                  y: function(d){ return d.value; },
                  showValues: true,
                  valueFormat: function(d){
                      return d3.format(',.0f')(d);
                  },
                  transitionDuration: 500,
                  xAxis: {
                      // axisLabel: 'X Axis',
                      rotateLabels: 90
                  },
                  yAxis: {
                      axisLabel: 'Y Axis',
                      axisLabelDistance: 30
                  }
              }
        };


        $scope.data = [
          {
          key: "Cumulative Return",
          values: [
            { "label" : "A" , "value" : 29.76 },
            { "label" : "B" , "value" : 30 },


            { "label" : "C" , "value" : 32.807 },
            { "label" : "D" , "value" : 46.459 },
            { "label" : "E" , "value" : 39 },
            { "label" : "F" , "value" : 48.07 },
            { "label" : "G" , "value" : 33.92 },
            { "label" : "H" , "value" : 45.13 }
          ]
        }
        ];

      }

      $scope.loadPieChart = function(){
        $scope.optionsPie = {
            chart: {
                type: 'pieChart',
                height: 500,
                x: function(d){


                  return d.key;},
                y: function(d){return d.y;},
                showLabels: true,
                duration: 500,
                labelThreshold: 0.01,
                labelSunbeamLayout: true,
                legend: {
                    margin: {
                        top: 5,
                        right: 35,
                        bottom: 5,
                        left: 0
                    }
                }
            }
        };



        $scope.dataPie = [
            {
                key: "One",
                y:30
            },
            {
                key: "Two",
                y: 2
            },
            {
                key: "Three",
                y: 9
            },
            {
                key: "Four",
                y: 7
            },
            {
                key: "Five",
                y: 4
            },
            {
                key: "Six",
                y: 3
            },
            {
                key: "Seven",
                y: .5
            }
        ];
      }
      // $scope.loadPieChart();
      // $scope.loadBarChart();

    }

]);





